<template>
  <div class="data-table-panel bg-white" id="content">
    <table class="table table-top-line-none">
      <tr class="text-left" v-for="(f, index) in prices" :key="f.id">
        
        <td width="6%" class="text-center text-accent">{{ index + 1 }}</td>
        <td width="13%">{{ f.num }}</td>
        <td width="25%">{{ f.membery.num }}&nbsp;{{ f.membery.named }}</td>
        <td width="15%">{{ ser_timed(f.start_time) }}</td>
        <td width="10%">{{ f.freight_num }}</td>
        <td width="6%"></td>
        <td width="10%" class="text-center">
          {{ ser_prices_status_pdf(f.first_pdf) }}
        </td>
        
        <td width="10%" align="center" class="text-center">
          <div class="flex-center">
            <i @click="option(f.id, 'edit')">
              <pencil></pencil>
            </i>
            <i @click="option(f.id, 'trash')">
              <remove></remove>
            </i>
          </div>
        </td>

      </tr>
    </table>

    <prices-trash @trashItem="trashPrices"></prices-trash>
  </div>
  
</template>

<script>

import view from '@/common/es/view'
import tool from '@/common/es/tool'

import service from '@/service'
import Pencil from '@/components/Option/Pencil.vue'
import Remove from '@/components/Option/Remove.vue'

import PricesTrash from '@/components/Modal/Trash.vue'

import conn from '@/service/conn'

export default {
  name: 'TagViewDataTable',
  components: {
    Pencil, Remove, PricesTrash
  },
  data() {
    return {
      prices: [ ]
    }
  },
  mounted() {
    // new Bscroll( document.getElementById('content'))
    
    conn.prices.getList_Prices().then(res => {
      this.prices = res
    })
  },
  methods: {
    ser_timed(v) {
      return tool.timed.ser_timed(v, true, true)
    },
    option(id, option) {
      if (option == 'trash') {
        tool.showModal('trashModal')
      } else if (option == 'edit') {
        
      }
    },
    trashPrices() {
      alert('执行删除')
    },
    ser_prices_status_pdf(v) {
      const html = '查看'
      return view.ser_prices_status_pdf(v, html, html)
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
.tag-td-wrapper
  padding: 0
  vertical-align: middle
  padding-top: 8px

.tag-color
  display: inline-block
  margin-top: 2px
  padding: $horizonS
  border-radius: $horizonS

</style>


